import React from 'react'

import Heading from './Heading';
import InfoBlock from './InfoBlock';

import amazonLogo from './adecco_amazon_logo.png';
import sercoLogo from './serco_ATO_logo.png';
const monashLogo = "https://d33wubrfki0l68.cloudfront.net/2c1ab1e3c74121c9e44b22832fc64a169ebb2d4d/ab5e5/assets/logos/monash.png"

let expData = 
[
    {
        title: "Industry Experience (Monash University) ",
        desc: "Working collaboratively in a team, utilising an Agile approach to develop a website using the CakePHP web-application framework, backed by a MySQL database, for a client wanting to start an organisation aimed at providing information and support for carers.",
        img: monashLogo,
        points: [
        "Utilising an Agile approach to software development by delivering functionality based on user stories in iterations", 
        "Liaison with the client in order to keep her up to date with the projects progression, answer questions.", 
        "Communicating in a team environment through both verbal and written forms of communication.",
        "Directing development by providing management and assistance for other team members."]   
    }
    ,
    {
        title: "Serco (ATO Contract)",
        desc: "Served a short-term contract on a part-time basis as a Customer Service Representitive at the Australian Taxation Office.",
        img: sercoLogo,
        points: [
        "Undertaking research and problem-solving techniques to propose solutions regarding clients taxation obligations.", 
        "Working collaboratively with team members to achieve the best outcomes for our clients.", 
        "Ability to meet set targets and implement feedback provided by team leader",
        "Experience working in a fast-paced ever-changing enviornment."
        ]

    },
    {
        title: "Adecco (Amazon Fulfillment Center)",
        desc: "Main responsibility at Amazon Fullfillment Center is fullfilling customer shipments.",
        img: amazonLogo,
        points: [
            "Time and task management skills essential to the role in order to fulfill shipment deadlines.", 
            "Collaborating with team members to fulfill priority shipments within strict deadlines.", 
            "Attention to detail and pride in my work to ensure shipment is well presented and protected.",
            ]
    }
]

function Experience() {
    return (
        <div className="container-fluid">
            <Heading heading="Experience" />
            <InfoBlock title={expData[0].title} desc={expData[0].desc} img={expData[0].img} points={expData[0].points} />
            <InfoBlock title={expData[1].title} desc={expData[1].desc} img={expData[1].img} points={expData[1].points} />
            <InfoBlock title={expData[2].title} desc={expData[2].desc} img={expData[2].img} points={expData[2].points} />
        </div>
    )
}

export default Experience



