import React from 'react'
import Heading from './Heading';
import InfoBlock from './InfoBlock';

import technicalImg from './code.jpg';
import ProjectSkillsImg from './images/project_planning.jpg'
import SoftImg from './images/teamwork.jpg';

let skillsData = 
    [
        {
            title: "Technical Skills",
            desc: "As a Bachelor of Information Technology graduate (majoring in software development). I have expreience and extensive knowledge utilising a wide variety of languages and frameworks. This section provides a summary of my current technical skills. ",
            img:technicalImg,
            points: [
            "Knowledge and experience using a variety of programming languages: HTML,CSS,Javascript,C++,Java,C,Ruby,PHP,SQL.", 
            "Experience using web frameworks such as: ReactJS, Laravel, Ruby on Rails and CakePHP.", 
            "Experience in Mobile Development creating apps for both iOS (Xcode and Swift) and Android (Android Studio and Java) at Monash University ",
            "Experience using Unreal Engine 4 (C++) and Unity (C#)."] 
        }
        ,
        {
            title: "Skills in a Team Project Environement",
            desc: "Listed is a summary of my key communication skills in a team project environment. These communication skills were vital in the success of the Industry Experience Program at Monash University.",
            img: ProjectSkillsImg,
            points: [
             "Using an Agile approach to software development by adding functionality to the website in iterations.",
             "Gathering business requirements from the client during the planning phase.",
             "Liason with the client to gather feedback and update the client on the progression of the project.",
             "Directing development by providing leadership and assistance for other team members.",
             "Being able to communicate with multiple stakeholders.", 
             "Ability to contribute to the back and front-end when required during development.",
             "Learn and use a new framework (CakePHP) and language (PHP) while developing the website by using online resources and documentation."]
    
        },
        {
            title: "Soft Skills",
            desc: "Below are key soft skills that I have aquired by working in a diverse range of industries.",
            img: SoftImg,
            points: [
            "Ability to simplify hard concepts to educate clients that are unfamilar with their taxation obligations.",
            "Time management including: Priortising work to meet deadlines, consistently coming on time to work and back from breaks, finding efficient ways to complete repetitive work.", 
            "Years of experience providing excellent customer service by providing quality and informative advice in a friendly and approachable manner.", 
            "Collaborating with team members to complete tasks in an efficient manner, and meet strict deadlines.",
            ]
        }
    ];

function Skills() {
    return (
        <div>
            <Heading heading="Skills"/>
            <InfoBlock title={skillsData[0].title} desc={skillsData[0].desc} img={skillsData[0].img} points={skillsData[0].points} />
            <InfoBlock title={skillsData[1].title} desc={skillsData[1].desc} img={skillsData[1].img} points={skillsData[1].points} />
            <InfoBlock title={skillsData[2].title} desc={skillsData[2].desc} img={skillsData[2].img} points={skillsData[2].points} />
        </div>
    )
}

export default Skills



