import React from 'react';
import { Link } from "react-router-dom";

//Component imports
//import Navbar from './Navbar';
import InfoCard from './Infocard';
import SkillsTable from './SkillsTable';

//Image Imports
import amazonLogo from './adecco_amazon_logo.png';
import profilepic from './linkedin-profile-img.jfif';
import './App.css'
import reactLogo from './logo.svg';
import expImage from './experience-image.jpg';
import cakePHPLogo from './images/cake-logo.svg';
import cPlusLogo from './images/C++.png';
import sercoLogo from './serco_ATO_logo.png';
const monashLogo = "https://d33wubrfki0l68.cloudfront.net/2c1ab1e3c74121c9e44b22832fc64a169ebb2d4d/ab5e5/assets/logos/monash.png"




// const CplusplusLogo = "https://upload.wikimedia.org/wikipedia/commons/thumb/1/18/ISO_C%2B%2B_Logo.svg/1200px-ISO_C%2B%2B_Logo.svg.png"
const JavaLogo = "https://1000logos.net/wp-content/uploads/2020/09/Java-Logo.png"
const htmlLogo = "https://seeklogo.com/images/H/html5-logo-EF92D240D7-seeklogo.com.png"
const cssLogo = "https://upload.wikimedia.org/wikipedia/commons/thumb/d/d5/CSS3_logo_and_wordmark.svg/1200px-CSS3_logo_and_wordmark.svg.png"
const javaScriptLogo = "https://upload.wikimedia.org/wikipedia/commons/6/6a/JavaScript-logo.png"
const laravelLogo = "https://upload.wikimedia.org/wikipedia/commons/thumb/9/9a/Laravel.svg/1200px-Laravel.svg.png"
//const cakePHPlogo = "https://seeklogo.com/images/C/cakephp-logo-C6870E4C5B-seeklogo.com.png"
const mySQLLogo = "https://download.logo.wine/logo/MySQL/MySQL-Logo.wine.png"


// Data Arrays
let expArray = [
  {
    title: "Amazon/Adecco",
    image: amazonLogo,
    points: [
      "Fulfilling customer shipments on strict deadlines.",
      "Collaborating with other team members to fulfill priority shipments.",
    ]
  },
  {
    title: "Industry Experience", image: monashLogo,
    points: [
      "Project based work experience with an agile approach to software development.",
      "Collaborating with team members to provide an excellent product for our client",
      "Liason with our client to gather feedback and update the client on project progression."
    ]
  },
  {
    title: "Serco/ATO", image: sercoLogo,
    points: [
      "Educating the Australian pubic regarding their tax obligations.",
      "Collaborating with colleagues to determine the best advice for the client.",
      "Excellent customer service over the phone."
    ]
  }

];

let skillsArray = [
  { image: cPlusLogo, name: "C++" },
  { image: JavaLogo, name: "Java" },
  { image: mySQLLogo, name: "SQL" },
  { image: htmlLogo, name: "HTML" },
  { image: cssLogo, name: "CSS" },
  { image: javaScriptLogo, name: "JavaScript" },
  { image: reactLogo, name: "React" },
  { image: laravelLogo, name: "Laravel" },
  { image: cakePHPLogo, name: "CakePHP" }

]


function Home() {
  return (
    <div className="container-fluid">
      <div className="row banner-img">
        <div className="row">
          {/* <Navbar/> */}
        </div>
        <div className="row justify-content-center">
          <div className="col-md my-5">
            <img className="img-fluid w-50 mx-auto d-block profile-pic rounded-circle" src={profilepic} />
          </div>
          <div className="col-md my-5">
            <h1 className="text-center fw-bolder">Damian Farinaccio</h1>
            <h2 className="text-center pt-1 fw-bold">Monash Graduate Software Developer</h2>
            <h4 className="text-center mt-3">Bachelor of Information Technology graduate majoring in Software Development, with extensive knowledge and experience developing with a vast array of programming languages and tools to create modern, fast and robust software and web applications. </h4>
          </div>
        </div>
      </div>


      <div className="flex-row mt-5">
        <h1 className="text-center">Experience</h1>
      </div>

      {/* Clean version of info cards */}
      <div className="row my-5">
        {expArray.map((item) => {
          return (
            <div className="d-flex col-xs col-sm-6 col-md-4 mt-4">
              <InfoCard title={item.title} image={item.image} points={item.points} />
            </div>
          )
        })}
      </div>
      <div className="row justify-content-center">
        <div className="col-sm-3 text-center">
          <Link className="btn btn-outline-success btn-lg text-nowrap" to="/experience">Learn More</Link>
        </div>
      </div>

      <div className="row mt-5">
        <h1 className="text-center pt-5">Skills</h1>
      </div>
      {/* Skills Table START */}
      <div className="row my-5 mx-5">
        <div className="col-md">
          <h4 className="px-3 my-5">Extensive knowledge in a wide variety of languages, frameworks, APIs and Libraries that are paramount in creating and maintaining modern applications. In addition, my excellent communication skills are vital in working in a professional team environment.</h4>
          <img className="img-fluid d-block rounded-3 mx-auto" src={expImage} />
        </div>
        <SkillsTable skills={skillsArray} />
      </div>
      <div className="row justify-content-center mb-5">
        <div className="col-sm-3 text-center">
          <Link className="btn btn-outline-success btn-lg text-nowrap" to="/skills">Learn More</Link>
        </div>
      </div>



      {/* Skills Table END */}
    </div>
  );
}


export default Home;



