import React from 'react'

function InfoBlock(props) {
    return (
        <div className="row m-5 border-top border-2">
            <div className="col-sm-12 col-md-12 col-lg-8 ">
                {/* title of work exp */}
                <h2 className="mt-3 fw-bold">{props.title}</h2>
                {/* description of work experience */}
                <p className="mt-3 fw-bold">{props.desc}</p>
                {/* Array of objects passed through as prop. Map function is performed on points array contained in object to create a bullet point for
                each point listed. */}
                <ul>
                    {props.points.map((point)=>{
                        return(
                            <li>{point}</li>
                        )
                    })}
                </ul>
            </div>
            <div className="col-sm-12 col-m-12 col-lg-4">
                <img className="img-fluid w-75 d-block mx-auto rounded-3 mt-5" src={props.img}></img>
            </div>
        </div>
    )
}

export default InfoBlock



